import * as yup from "yup";
import { REGEX_EMAIL, REGEX_PASSWORD, REGEX_PHONE } from "./regex";
export const SCHEMA_LOGIN = {
  username: yup
    .string()
    .trim()
    .required("Vui lng nhp tn ng nhp")
    .min(3, "Tn ng nhp phi ti thiu 3 k t")
    .max(30, "Tn ng nhp ti a 30 k t"),
  password: yup
    .string()
    .trim()
    .required("Vui lng nhp mt khu")
    .matches(
      REGEX_PASSWORD,
      "Mt khu phi ti thiu 8 k t t nht mt k t hoa, mt k t thng, mt k t s v mt k t c bit"
    ),
};
export const SCHEMA_FORGOT_PASSWORD = {
  email: yup
    .string()
    .trim()
    .required("Vui lng nhp email")
    .matches(REGEX_EMAIL, "Email khng hp l"),
};


export const SCHEMA_VOUCHER = {
  name: yup
  .string()
  .trim()
  .required("Vui lng nhp tn"),
  code: yup
  .string()
  .trim()
  .required("Vui lng nhp m"),
  money:yup.number("Khng hp l").min(0,"Khng hp l").nullable(true),
  slot:yup.number("Khng hp l").min(0,"Khng hp l").nullable(true),

};
export const SCHEMA_CHANGE_PASSWORD = {
  current_password: yup.string().trim().required("Vui lng nhp mt khu c"),
  new_password: yup
    .string()
    .trim()
    .required("Vui lng nhp mt mi")
    .matches(
      REGEX_PASSWORD,
      "Mt khu phi ti thiu 8 k t t nht mt k t hoa, mt k t thng, mt k t s v mt k t c bit"
    ),
  retype_new_password: yup
    .string()
    .trim()

    .required("Vui lng nhp li mt khu mi")
    .oneOf([yup.ref(`new_password`), null], "Mt khu mi khng khp"),
};
export const SCHEMA_RESET_PASSWORD = {
  new_password: yup
    .string()
    .trim()
    .required("Vui lng nhp mt mi")
    .matches(
      REGEX_PASSWORD,
      "Mt khu phi ti thiu 8 k t t nht mt k t hoa, mt k t thng, mt k t s v mt k t c bit"
    ),
  retype_new_password: yup
    .string()
    .required("Vui lng nhp li mt khu mi")
    .oneOf([yup.ref(`new_password`), null], "Mt khu mi khng khp"),
};
